import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Input from '@mui/material/Input';

const dispatchedEvents = [];

const SrsMuiInput = function (props, ref) {
  return (
    <Input {...props}>
      <slot />
    </Input>
  );
}

const styles = `
  /* Custom styles for the Input component */
`;

const props = [
  {
    name: "defaultValue",
    defaultValue: "",
    type: "string",
  },
  {
    name: "value",
    defaultValue: "",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "required",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "placeholder",
    defaultValue: "",
    type: "string",
  },
  {
    name: "type",
    defaultValue: "text",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Input if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiInput), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
